const reducer = (state, action) => {
  switch (action.type) {
    case "SET_FIELD_VALIUE":
      return {
        ...state,
        values: {
          ...state.values,
          ...action.payload
        }
      };
    case "SET_FIELD_TOUCHED":
      return {
        ...state,
        touched: {
          ...state.touched,
          ...action.payload
        }
      };
    case "SET_ERRORS":
      return {
        ...state,
        errors: action.payload
      };

    default:
      return state;
  }
};

export default reducer;
